@import url(https://fonts.googleapis.com/css2?family=KoHo:wght@300&display=swap);
body{margin:0;font-family:"KoHo", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#212121;color:#f0f0f0;box-sizing:border-box}input:focus,select:focus,textarea:focus,button:focus{outline:none}.material-icons{cursor:pointer;color:#ab47bc;transition:color 250ms ease-in-out}.material-icons:hover{color:#d500f9}

.container{width:100vw;height:100vh;padding:32px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;box-sizing:border-box}

.button{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.button{-webkit-user-select:none;-ms-user-select:none;user-select:none}.button{font-size:20px;cursor:pointer;padding:8px 16px;border-radius:4px}.button-normal{background-color:#ab47bc;color:#f0f0f0;transition:background-color 250ms ease-in-out;border:2px solid transparent}.button-normal:hover{background-color:#d500f9}.button-outlined{border:2px solid #ab47bc;color:#ab47bc;transition:all 250ms ease-in-out}.button-outlined:hover{border-color:#d500f9;color:#d500f9}

.actions{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;padding:0 64px}.actions-buttons{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin-right:-16px}.actions-buttons>*{margin-right:16px}

.text-input{-webkit-appearance:none;-o-appearance:none;appearance:none}.text-input{padding:4px 8px;font-size:16px;border-radius:4px;border:1px solid #ab47bc;background-color:transparent;transition:border-color 250ms ease-in-out;color:#f0f0f0}.text-input::-webkit-input-placeholder{color:#9d9d9d}.text-input:-ms-input-placeholder{color:#9d9d9d}.text-input::placeholder{color:#9d9d9d}.text-input:focus{border-color:#d500f9}

.sudoku-input{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:100%}.sudoku-input-area{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;margin:8px 0;overflow:hidden;height:0;transition:height 250ms ease-in-out}.sudoku-input-area-active{height:48px}.sudoku-input-area>*{margin-right:8px}

.sudoku-result{padding:16px 16px;height:0;font-size:16px;transition:height 250ms ease-in-out;width:100%}.sudoku-result-danger{color:#ec407a}.sudoku-result-success{color:#a5d6a7}.sudoku-result-showing{height:64px}

.digit-picker{box-shadow:0px 0px 5px 5px rgba(0,0,0,0.4)}.digit-picker{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:5px;background-color:#383838;border-radius:4px;color:#f0f0f0}.digit-picker-row{display:-webkit-flex;display:flex}.digit-picker-btn,.digit-picker-clear-btn{border:1px solid #f0f0f0;border-radius:4px;cursor:pointer;font-size:16px;transition:all 250ms ease-in-out}.digit-picker-btn:hover,.digit-picker-clear-btn:hover{border-color:#d500f9;color:#d500f9}.digit-picker-btn{padding:4px;margin:4px;width:24px;height:24px}.digit-picker-clear-btn{margin:4px;padding:4px;height:24px}

.sudoku-digit{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.sudoku-digit{position:relative;width:32px;height:32px;border:1px solid transparent;background-color:#212121;cursor:pointer;font:inherit;font-size:24px;transition:border-color 250ms ease-in-out;color:#f0f0f0}@media (min-width: 768px){.sudoku-digit{width:64px;height:64px}}.sudoku-digit:hover{border-color:#ab47bc}.sudoku-digit-editable{color:#ab47bc}.sudoku-digit-error{background-color:rgba(236,64,122,0.2)}.sudoku-digit-picker{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);z-index:1;cursor:auto;cursor:initial}.top-middle,.bottom-middle{border-left:1px solid #383838;border-right:1px solid #383838}.center-left,.center-right{border-top:1px solid #383838;border-bottom:1px solid #383838}.center-middle{border:1px solid #383838}

.sudoku-square{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;border:1px solid #f0f0f0;border-radius:4px}.sudoku-square-row{display:-webkit-flex;display:flex}

.sudoku{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.sudoku-row{display:-webkit-flex;display:flex}

